//--------------------------------------------------------------
// MENUS
//--------------------------------------------------------------

//-----------------------------------------
// Menu Toggles
//-----------------------------------------
.mobile-menu-toggle {
	display: block;
	margin: 0 auto;
	width: rem(200);

	@include mq-min($tablet-landscape-up) {
		display: none;
	}

	.mobile-nav-toggle-style-hamburger & {

		@include margin-padding-reset;
		background-color: transparent;
		height: rem(21);

		position: absolute;
		right: $size__padding;
		top: $size__padding;
		width: rem(25);
		z-index: 6;

		&:hover,
		&:active,
		&:focus {
			background-color: transparent;
		}

		@include element(bar) {
			background-color: $color__toggle-background;
			display: block;
			height: rem(3);
			left: 0;
			margin: auto;
			position: absolute;
			transition: $transition__transform;
			width: rem(25);

			//-----------------------------------------
			// Hamburger bars
			//-----------------------------------------
			&.bar-1 {
				top: 0;
			}

			&.bar-2 {
				bottom: 0;
				top: 0;
			}

			&.bar-3 {
				bottom: 0;
			}
		}

		// Hide the "Menu" text.
		.menu-text {
			display: block;
			text-indent: -9999px;
		}
	}

	.mobile-nav-toggle-style-hamburger.admin-bar & {
		top: ($size__padding * 2) + rem(46);

		@include mq-min($wp-admin-bar) {
			top: ($size__padding + 2) + rem(32);
		}
	}
}

//-----------------------------------------
// Menu Toggle Positioning
//-----------------------------------------
body:not(.mobile-nav-style-default) {
	// Add extra padding at the top to give the menu breathing room.
	.main-navigation {

		@include media(null, $tablet-landscape-up) {
			padding-top: $size__padding * 4;
		}
	}

	// Position the menu close toggle.
	&.menu-open {

		.mobile-menu-toggle {
			position: absolute;
			top: $size__padding;
			z-index: 10;
		}

		&.admin-bar {

			.mobile-menu-toggle {
				top: ($size__padding * 2) + rem(46);

				@include mq-min($wp-admin-bar) {
					top: ($size__padding + 2) + rem(32);
				}
			}
		}
	}
}

//-----------------------------------------
// Toggled Bars
//-----------------------------------------
.mobile-nav-toggle-style-hamburger {

	&.menu-open {

		.mobile-menu-toggle__bar {

			&.bar-1 {
				bottom: 0;
				transform: rotate(45deg);
			}

			&.bar-2 {
				display: none;
			}

			&.bar-3 {
				top: 0;
				transform: rotate(-45deg);
			}
		}
	}
}

//-----------------------------------------
// General Menu Styles
//-----------------------------------------
.menu {

	@include list-reset;

	@include mq-min($tablet-landscape-up) {
		text-align: center;

		.header-style-side &,
		.widget_nav_menu & {
			text-align: left;
		}
	}

	> .menu-item {

		@include mq-min($tablet-landscape-up) {
			display: inline-block;

			.header-style-side &,
			.widget_nav_menu & {
				display: block;
			}

			+ .menu-item {
				margin-left: rem(20);

				.header-style-side &,
				.widget_nav_menu & {
					margin-left: 0;
				}
			}
		}
	}
}

.menu-item {

	> a {
		display: block;
		padding: rem(5) 0;
		text-decoration: none;

		@include mq-min($tablet-landscape-up) {
			padding-left: rem(10);
			padding-right: rem(10);

			.header-style-side &,
			.widget_nav_menu & {
				padding-left: 0;
				padding-right: 0;
			}
		}
	}

	&.menu-item-has-children {
		position: relative;

		@include mq-min($tablet-landscape-up) {

			> a {
				padding-right: rem(40);
			}
		}
	}
}

//-----------------------------------------
// Sub Menus
//
// The header style side menu should
// function like a mobile menu, so most styles
// are scoped to the default & short headers.
//-----------------------------------------
.sub-menu {

	@include list-reset;

	display: none;
}

// Not the side header style.
body:not(.header-style-side) {

	.reverse-expand {

		.sub-menu {
			left: auto;
			right: 0;

			.sub-menu {
				left: -100%;
			}
		}
	}

	.sub-menu {

		@include mq-min($tablet-landscape-up) {
			background-color: $color__sub-menu-background;
			left: 0;
			min-width: rem(175);
			position: absolute;
			text-align: left;
			top: 101%;
			width: 100%;

			.sub-menu {
				left: 100%;
				top: 0;
			}
		}
	}
}

//-----------------------------------------
// Sub Menu Toggles
//-----------------------------------------
.sub-level-toggle {

	@include size(rem(34), rem(34));

	background-color: transparent;
	position: absolute;
	right: 0;
	top: 0;

	&:hover,
	&:active,
	&:focus {
		background-color: transparent;
	}

	.sub-level-toggle-span {

		@include absolute-center;

		background-color: $color__menu-sub-toggle;
		height: rem(2);
		margin: auto;
		width: rem(12);
	}

	.span-2 {
		transform: rotate(90deg);
		transition: $transition__transform;

		.toggled > & {
			transform: rotate(0);
		}
	}

	// The chevron sub menu toggle.
	.sub-menu-toggle-style-chevron & {

		.sub-level-toggle-span {
			bottom: 0;
			left: auto;
			position: absolute;
			right: auto;
			top: 25%;
			transform: rotate 0.3s ease-in-out;
			transition: $transition__transform;
			width: rem(11.5);
		}

		.span-1 {
			right: 49%;
			transform: rotate(45deg);
			transform-origin: 100%;
		}

		.span-2 {
			left: 49%;
			transform: rotate(-45deg);
			transform-origin: 0;
		}

		&.toggled {
			transform: rotateX(180deg);
		}
	}
}



