//--------------------------------------------------------------
// TYPOGRAPHY
//--------------------------------------------------------------

//-----------------------------------------
// Font Palette
//-----------------------------------------
$font-awesome: FontAwesome; // stylelint-disable-line value-keyword-case
$source-code-pro: "Source Code Pro", Courier, monospace; // stylelint-disable-line value-keyword-case
$source-sans-pro: "Source Sans Pro", sans-serif;

//-------------------------------
// Typography
//-------------------------------
$font__body: $source-sans-pro;
$font__body-line-height: 1.5;
$font__body-weight: 400;
$font__body-size: $base-context;
$font__code: $source-code-pro;
$font__code-line-height: 1.6;
$font__code-size: 0.875em; // ~14px

//-------------------------------
// Social Icons
//-------------------------------
$font__social-icons: $font-awesome;



