//--------------------------------------------------------------
// HEADER
//--------------------------------------------------------------
// stylelint-disable color-named
.site-header {
	padding: $size__padding;

	.header-style-default & {

		@include mq-min($tablet-landscape-up) {
			text-align: center;
		}
	}
}

.site-title {

	@extend h1 !optional;

	font-weight: 700;
	line-height: 1;
	margin-bottom: 0.25em;
}

.site-description {
	margin-bottom: 0;
}

//-----------------------------------------
// Header Style Short
//-----------------------------------------
.header-style-short {

	@include mq-min($tablet-landscape-up) {

		.site-header {
			display: flex;
			justify-content: space-between;
		}

		.main-navigation {
			margin-left: auto;
			padding-right: 0;
		}
	}
}

//-----------------------------------------
// Header Style Side
//-----------------------------------------
.header-style-side {

	@include mq-min($tablet-landscape-up) {

		.site-header {
			background-color: color(white);
			bottom: 0;
			box-shadow: $color__header-style-side-box-shadow;
			overflow-y: scroll;
			padding-top: $size__padding * 2;
			position: fixed;
			top: 0;
			width: rem(300);
		}

		.main-navigation {
			padding-left: 0;
			padding-right: 0;
		}

		&.admin-bar {

			.site-header {
				top: rem(32);
			}
		}
	}
}

//-----------------------------------------
// Logo
//-----------------------------------------
.has-logo {

	.site-title,
	.site-description {

		@extend .screen-reader-text !optional;
	}
}

.custom-logo-link {
	display: block;
	line-height: 1;
}

.custom-logo {
	line-height: 1;

	.mobile-nav-toggle-style-button & {

		@include center-block;
	}
}



